// hero del principio nuestro flayer
.contenedor-hero {
    background-color: $blanco;
}

.hero-img {
    box-sizing: content-box;
    background-image: url(../img/hero3.webp);
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: 0rem 0rem 2rem 0rem inset rgba($color: $negro, $alpha: 0.2);
    background-size: cover;
    .hero-propiedades {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        max-width: 100%;
        padding: 10rem 0;
        h1 {
            color: $blanco;
        }
    }
    .slogan {
        color: $blanco;
    }
}