.recoleccion {
    margin: 8rem 0;
    display: flex;
    border-top: 2px solid $verdeOscuro;
    border-bottom: 2px solid $verdeOscuro;
    .imagen-recoleccion {
        display: flex;
        align-items: center;
        height: 40rem;
        flex-basis: calc(70%);
        background-image: url(../img/ServicioTransporte.webp);
        background-position: center;
        background-size: cover;
        h2 {
            color: $blanco;
            align-items: center;
            width: 100%;
            padding: 4rem 2rem;
            background-color: rgba($color: $negro, $alpha: 0.5);
        }
    }
    .info {
        display: flex;
        align-items: center;
        flex-direction: column;
        flex-basis: calc(30%);
        background-color: $blanco;
        padding: 3%;
        p {
            font-size: 2rem;
            text-align: justify;
            padding-bottom: 3rem;
            border-bottom: 2px solid $verdeOscuro;
        }
        a {
            color: $blanco;
            font-size: 2rem;
            text-shadow: -0.1rem -0.1rem rgba($color: $negro, $alpha: 0.3);
            display: inline-block;
            width: 20rem;
            padding: 0.5rem;
            text-align: center;
            background-color: $azul;
            border: 1px solid $azulOscuro;
            border-bottom: 1.5px solid rgba($color: $blanco, $alpha: 0.2);
            border-radius: 0.5rem;
            box-shadow: 0rem 0.3rem $azulOscuro;
        }
    }
}