.contenedor-articulos {
    border-top: 2px solid $verdeOscuro;
    border-bottom: 1px solid $verdeOscuro;
    text-align: center;
    background-image: url(../img/destruccion3.webp);
    background-size: cover;
    background-position: center;
    height: 30rem;
    display: flex;
    align-items: center;
    margin-bottom: 4rem;
    .Dispocision {
        // border-right: 2px solid $verdeOscuro;
        // border-left: 2px solid $verdeOscuro;
        background-color: rgba($color: $blanco, $alpha: 0.6);
        height: 80%;
        h2 {
            text-transform: uppercase;
            // background-color: rgba($color: $negro, $alpha: 0.4);
        }
        p {
            padding-bottom: 4rem;
            // background-color: rgba($color: $negro, $alpha: 0.4);
        }
        a {
            display: inline-block;
            background-color: $rojo;
            padding: 1rem 2rem;
            color: $blanco;
            border: 2px solid $blanco;
        }
    }
}