.contenedor-header {
    max-width: 100%;
    background-color: $blanco;
}

.logo-info {
    padding: 1rem 1rem;
    border-bottom: 2px solid $verdeOscuro;
    @include grid(2, $separacion);
    align-items: center;
}

.logo {
    max-width: 20rem;
}

.contacto-header {
    text-align: end;
    p {
        padding: 0;
        margin: 0;
    }
}

h1 {
    font-family: $TerceraFuente;
}

.contacto {
    p {
        display: inline;
        padding-right: 2rem;
        font-family: $TerceraFuente;
        font-size: 1.8rem;
    }
    p:last-child {
        padding: none;
    }
    span {
        i {
            color: $verdeOscuro;
            font-weight: bold;
        }
    }
}

//estilos del menu o navegador
.navegacion-principal {
    text-align: center;
    background-color: $verde;
    .contenedor-navegacion {
        @include grid(5, 0);
        position: relative;
        a {
            font-size: 1.8rem;
            position: relative;
            display: table-cell;
            color: $verdeClaro;
            margin: 0.8rem 0;
            padding: 1rem 0;
            border-right: 2px solid $verdeClaro;
        }
        a:last-of-type {
            border-right: none;
        }
    }
    .activa {
        height: 100%;
        position: absolute;
        color: $verde !important;
        font-weight: bold;
        background-color: #fcf9f3;
        padding: 2rem !important;
        margin: 0 !important;
        border: none !important;
    }
    .activa::after {
        position: absolute;
        top: -7px;
        left: 0;
        height: 7px;
        width: 100%;
        background: #fff;
        content: "";
        border-radius: 1rem 1rem 0 0;
    }
    .activa::before {
        display: none;
    }
}