    .footer {
        background-color: $verdeMilitar;
        padding: 0;
        margin: 0;
        .clientes {
            h2 {
                padding-top: 2rem;
                color: $blanco;
                text-align: center;
            }
            ul {
                padding: 0;
                margin: 2rem 0;
                background-color: $blanco;
                @include grid(6, 0);
                align-items: center;
                height: 10rem;
                li {
                    list-style: none;
                    border-right: 2px solid $verdeOscuro;
                    text-align: center;
                }
                li:last-child {
                    border: none;
                }
            }
        }
        .info-footer {
            border-top: 1px solid $negro;
            border-bottom: 1px solid $negro;
            .flex-info {
                @include grid(3, 0);
                align-items: center;
            }
            p {
                padding: 2rem 4rem;
                border-right: 1px solid $negro;
                font-size: 2rem;
            }
            p:last-child {
                border: none;
            }
            //     .footer-contacto {}
        }
        .politicasweb {
            margin: 0 2rem;
            padding: 1rem;
            p {
                text-align: center;
                padding: 0;
                margin: 0;
            }
        }
    }