    .servicios-contenedor {
        @include grid(3, 0.2rem);
        .servicio {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .elemento-servicio {
                flex: 0 0 calc(50% - 1rem);
            }
            .contenedor-imagen {
                box-shadow: 0rem 0rem 3rem 2rem inset rgba($color: $negro, $alpha: 0.5);
                h2 {
                    padding: 2rem 1rem 0 1rem;
                    font-size: 2rem;
                    color: $blanco;
                    text-transform: uppercase;
                }
            }
            .info {
                background: linear-gradient(135deg, $verdeOscuro 0%, $verde 50%, $verdeOscuro 100%);
                height: 20rem;
                padding: 1.5rem 2rem;
                p {
                    margin: 0;
                    color: $blanco;
                    font-size: 1.8rem;
                    text-align: justify;
                    padding-bottom: 2rem;
                    flex: 0 0 calc(50% - 1rem);
                }
                .boton {
                    display: inline-block;
                    width: 200px;
                    padding: 0.8rem;
                    color: $blanco;
                    border: 3px solid $blanco;
                    flex: 0 0 calc(50% - 1rem);
                }
            }
            // .info:nth-child(2) {
            //     background-color: $azul;
            // }
        }
        .servicio:nth-child(2n) {
            .info {
                background: linear-gradient(135deg, $azulOscuro 0%, $azul 50%, $azulOscuro 100%);
            }
        }
    }
    
    .servicio-reciclaje {
        .imagen {
            height: 20rem;
            background-image: url(../img/reciclaje.jpg);
            background-size: cover;
            background-position: center;
        }
    }
    
    .servicio-borrado {
        .imagen {
            height: 20rem;
            background-image: url(../img/discos.jpg);
            background-size: cover;
            background-position: center;
        }
    }
    
    .servicio-desensamble {
        .imagen {
            height: 20rem;
            background-image: url(../img/Servidores.jpg);
            background-size: cover;
            background-position: center;
        }
    }