html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #fcf9f3;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.oscurecer {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

a,
h1,
h2,
h3,
p {
  font-family: "PT Sans", sans-serif;
}

.contenedor-header {
  max-width: 100%;
  background-color: #fff;
}

.logo-info {
  padding: 1rem 1rem;
  border-bottom: 2px solid #086d05;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
  align-items: center;
}

.logo {
  max-width: 20rem;
}

.contacto-header {
  text-align: end;
}
.contacto-header p {
  padding: 0;
  margin: 0;
}

h1 {
  font-family: "PT Sans", sans-serif;
}

.contacto p {
  display: inline;
  padding-right: 2rem;
  font-family: "PT Sans", sans-serif;
  font-size: 1.8rem;
}
.contacto p:last-child {
  padding: none;
}
.contacto span i {
  color: #086d05;
  font-weight: bold;
}

.navegacion-principal {
  text-align: center;
  background-color: #668B5E;
}
.navegacion-principal .contenedor-navegacion {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0;
  position: relative;
}
.navegacion-principal .contenedor-navegacion a {
  font-size: 1.8rem;
  position: relative;
  display: table-cell;
  color: #B3DEB4;
  margin: 0.8rem 0;
  padding: 1rem 0;
  border-right: 2px solid #B3DEB4;
}
.navegacion-principal .contenedor-navegacion a:last-of-type {
  border-right: none;
}
.navegacion-principal .activa {
  height: 100%;
  position: absolute;
  color: #668B5E !important;
  font-weight: bold;
  background-color: #fcf9f3;
  padding: 2rem !important;
  margin: 0 !important;
  border: none !important;
}
.navegacion-principal .activa::after {
  position: absolute;
  top: -7px;
  left: 0;
  height: 7px;
  width: 100%;
  background: #fff;
  content: "";
  border-radius: 1rem 1rem 0 0;
}
.navegacion-principal .activa::before {
  display: none;
}

.contenedor-hero {
  background-color: #fff;
}

.hero-img {
  box-sizing: content-box;
  background-image: url(../img/hero3.webp);
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 0rem 0rem 2rem 0rem inset rgba(0, 0, 0, 0.2);
  background-size: cover;
}
.hero-img .hero-propiedades {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 100%;
  padding: 10rem 0;
}
.hero-img .hero-propiedades h1 {
  color: #fff;
}
.hero-img .slogan {
  color: #fff;
}

.dato-curioso1 {
  padding: 7rem 3rem 1rem 3rem;
}
.dato-curioso1 h2 {
  text-align: center;
  color: #086d05;
}
.dato-curioso1 p {
  text-align: justify;
  font-size: 1.8rem;
  padding-bottom: 3rem;
}
.dato-curioso1 p:last-child {
  padding-bottom: 0;
}

.servicios-contenedor {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.2rem;
}
.servicios-contenedor .servicio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.servicios-contenedor .servicio .elemento-servicio {
  flex: 0 0 calc(50% - 1rem);
}
.servicios-contenedor .servicio .contenedor-imagen {
  box-shadow: 0rem 0rem 3rem 2rem inset rgba(0, 0, 0, 0.5);
}
.servicios-contenedor .servicio .contenedor-imagen h2 {
  padding: 2rem 1rem 0 1rem;
  font-size: 2rem;
  color: #fff;
  text-transform: uppercase;
}
.servicios-contenedor .servicio .info {
  background: linear-gradient(135deg, #086d05 0%, #668B5E 50%, #086d05 100%);
  height: 20rem;
  padding: 1.5rem 2rem;
}
.servicios-contenedor .servicio .info p {
  margin: 0;
  color: #fff;
  font-size: 1.8rem;
  text-align: justify;
  padding-bottom: 2rem;
  flex: 0 0 calc(50% - 1rem);
}
.servicios-contenedor .servicio .info .boton {
  display: inline-block;
  width: 200px;
  padding: 0.8rem;
  color: #fff;
  border: 3px solid #fff;
  flex: 0 0 calc(50% - 1rem);
}
.servicios-contenedor .servicio:nth-child(2n) .info {
  background: linear-gradient(135deg, #216da0 0%, #64b3d3 50%, #216da0 100%);
}

.servicio-reciclaje .imagen {
  height: 20rem;
  background-image: url(../img/reciclaje.jpg);
  background-size: cover;
  background-position: center;
}

.servicio-borrado .imagen {
  height: 20rem;
  background-image: url(../img/discos.jpg);
  background-size: cover;
  background-position: center;
}

.servicio-desensamble .imagen {
  height: 20rem;
  background-image: url(../img/Servidores.jpg);
  background-size: cover;
  background-position: center;
}

.recoleccion {
  margin: 8rem 0;
  display: flex;
  border-top: 2px solid #086d05;
  border-bottom: 2px solid #086d05;
}
.recoleccion .imagen-recoleccion {
  display: flex;
  align-items: center;
  height: 40rem;
  flex-basis: calc(70%);
  background-image: url(../img/ServicioTransporte.webp);
  background-position: center;
  background-size: cover;
}
.recoleccion .imagen-recoleccion h2 {
  color: #fff;
  align-items: center;
  width: 100%;
  padding: 4rem 2rem;
  background-color: rgba(0, 0, 0, 0.5);
}
.recoleccion .info {
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-basis: calc(30%);
  background-color: #fff;
  padding: 3%;
}
.recoleccion .info p {
  font-size: 2rem;
  text-align: justify;
  padding-bottom: 3rem;
  border-bottom: 2px solid #086d05;
}
.recoleccion .info a {
  color: #fff;
  font-size: 2rem;
  text-shadow: -0.1rem -0.1rem rgba(0, 0, 0, 0.3);
  display: inline-block;
  width: 20rem;
  padding: 0.5rem;
  text-align: center;
  background-color: #64b3d3;
  border: 1px solid #216da0;
  border-bottom: 1.5px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.5rem;
  box-shadow: 0rem 0.3rem #216da0;
}

.contenedor-articulos {
  border-top: 2px solid #086d05;
  border-bottom: 1px solid #086d05;
  text-align: center;
  background-image: url(../img/destruccion3.webp);
  background-size: cover;
  background-position: center;
  height: 30rem;
  display: flex;
  align-items: center;
  margin-bottom: 4rem;
}
.contenedor-articulos .Dispocision {
  background-color: rgba(255, 255, 255, 0.6);
  height: 80%;
}
.contenedor-articulos .Dispocision h2 {
  text-transform: uppercase;
}
.contenedor-articulos .Dispocision p {
  padding-bottom: 4rem;
}
.contenedor-articulos .Dispocision a {
  display: inline-block;
  background-color: #c90505;
  padding: 1rem 2rem;
  color: #fff;
  border: 2px solid #fff;
}

.footer {
  background-color: #0E533A;
  padding: 0;
  margin: 0;
}
.footer .clientes h2 {
  padding-top: 2rem;
  color: #fff;
  text-align: center;
}
.footer .clientes ul {
  padding: 0;
  margin: 2rem 0;
  background-color: #fff;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0;
  align-items: center;
  height: 10rem;
}
.footer .clientes ul li {
  list-style: none;
  border-right: 2px solid #086d05;
  text-align: center;
}
.footer .clientes ul li:last-child {
  border: none;
}
.footer .info-footer {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
}
.footer .info-footer .flex-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0;
  align-items: center;
}
.footer .info-footer p {
  padding: 2rem 4rem;
  border-right: 1px solid #000;
  font-size: 2rem;
}
.footer .info-footer p:last-child {
  border: none;
}
.footer .politicasweb {
  margin: 0 2rem;
  padding: 1rem;
}
.footer .politicasweb p {
  text-align: center;
  padding: 0;
  margin: 0;
}